/* open-sans-regular - cyrillic-ext_cyrillic_latin */
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/open-sans-v16-cyrillic-ext_cyrillic_latin-regular.eot");
  /* IE9 Compat Modes */
  src: local("Open Sans Regular"), local("OpenSans-Regular"),
    url("../fonts/open-sans-v16-cyrillic-ext_cyrillic_latin-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */
      url("../fonts/open-sans-v16-cyrillic-ext_cyrillic_latin-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../fonts/open-sans-v16-cyrillic-ext_cyrillic_latin-regular.woff")
      format("woff"),
    /* Modern Browsers */
      url("../fonts/open-sans-v16-cyrillic-ext_cyrillic_latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../fonts/open-sans-v16-cyrillic-ext_cyrillic_latin-regular.svg#OpenSans")
      format("svg");
  /* Legacy iOS */
}

/* open-sans-700 - cyrillic-ext_cyrillic_latin */
