  font-style: normal;
  font-weight: 700;
  src: url("../fonts/open-sans-v16-cyrillic-ext_cyrillic_latin-700.eot");
  /* IE9 Compat Modes */
  src: local("Open Sans Bold"), local("OpenSans-Bold"),
    url("../fonts/open-sans-v16-cyrillic-ext_cyrillic_latin-700.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */
      url("../fonts/open-sans-v16-cyrillic-ext_cyrillic_latin-700.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../fonts/open-sans-v16-cyrillic-ext_cyrillic_latin-700.woff")
      format("woff"),
    /* Modern Browsers */
      url("../fonts/open-sans-v16-cyrillic-ext_cyrillic_latin-700.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../fonts/open-sans-v16-cyrillic-ext_cyrillic_latin-700.svg#OpenSans")
      format("svg");
  /* Legacy iOS */
}
